@import "highlight";
@import "bulma/_all";
@import "fa/_all";

.post-list-heading {
    @extend .title; }

.post-list {
    .post-header {
        margin-bottom: 10px;

        .post-link {
            @extend .title;
            @extend .is-4; }
        .post-meta {
            @extend .subtitle;
            @extend .is-6; } } }

.post-header {
    margin-bottom: 20px;

    .post-title {
        @extend .title;
        @extend .is-2; }
    .post-meta {
        @extend .subtitle;
        @extend .is-6; } }

.post-content {
    @extend .content; }

.author {
    .card {
      box-shadow: None; } }


.site-content {
  margin-top: 25px; }

.highlight pre {
  text-align: left; }

.menu-item span {
  margin-right: 5px {
    display: inline-block; } }

.footer {
  margin-top: 15px; }

.author {
 .social {
   margin-top: 8px {
    margin-bottom: 15px; } } }
